<!--InfBox组件doc-->
<!--
实用方式：  参考
<infBox :items="infoIems" title="计量点明细"></infBox>

参数说明：
title: 标题
items：{array} 数据列项数组 格式： [{ label: '计量点数量',num: 22,}],


-->

<template>
    <div class="over-box">
        <div class="title">{{title}}</div>
        <div class="items">
            <div v-for="item in items" class="item-li">
                <span class="label">{{item.label}}：</span>
                <span class="count" @click="click(item)">{{item.num}}</span>
                <span class="label"> 个</span>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        name: 'InfBox',
        props: {
            title: {
                type: String,
                default: '',
            },
            items: {
                type: Array,
                default: () => {
                    return [{
                        label: '计量点数量',
                        num: 22,
                    }, {
                        label: '采集终端数量',
                        num: 45,
                    }]
                },
            },
        },
        data() {
            return {}
        },
        computed: {
            itemWith() {
                return {
                    width: (1 / this.items.length) * 100 + '%'
                }
            }
        },
        methods: {
            click(item) {
                this.$emit("click", item)
            }
        }
    };
</script>
<style scoped lang="scss">
    .over-box {
        background: rgba(46, 74, 247, 0.03);
        border-radius: 2px;
        border: 1px solid rgba(46, 74, 247, 0.31);
        padding: 10px 20px;
        color: #333;

        .title {
            font-family: 'PFC'
        }
    }

    .items {
        display: flex;
        align-items: center;

        justify-content: space-between;

        .item-li {
            line-height: 24px;
        }

        .count {
            cursor: pointer;
            text-decoration: underline;
            font-size: 14px;
            color: #000;
            font-family: "PFC"
        }
    }
</style>