<template>
    <div class="search" @keyup.enter="search">
        <form-create v-model="formObj" :rule="rule" :option="options" @btn-search="search" @btn-reset="reset">
        </form-create>
        <div class="li">
            <el-button v-if="!showAddBtn" type="primary" @click="down">导 出</el-button>
            <el-button v-if="showAddBtn" type="primary" @click="add">新 增</el-button>
            <el-button v-if="showAddBtn" type="default" @click="down">导 出</el-button>
        </div>
    </div>
</template>
<script>
    //接口
    import {
        getGlList,
        getKpList,
        getKcList,
        getPointsList
    } from "@/api/archives.js";
    import {
        parseTime,
        getPrevMonth
    } from "@/utils/index";
    import form from "../../../analyse/confirmPower/billConfirm/components/billCreate/SearchInputsCreate.vue";
    export default {
        extends: form,
        props: {
            type: {
                type: Number,
                default: 1,
            },
            showAddBtn: Boolean, //显示新增按钮
        },
        data() {
            return {
                options: {
                    form: {
                        labelPosition: 'left',
                        labelWidth: '95px'
                    },
                    submitBtn: false,
                    resetBtn: false
                },
                //表单生成规则
                rule: [{
                    type: 'select',
                    field: "kcCode",
                    title: '公司名称：',
                    value: '',
                    options: [],
                    props: {
                        filterable: true,
                    },
                    on: {
                        change: (item) => {
                            //清空平台 计量点 线路
                            const formData = this.formObj.bind();
                            formData.glCode = '';
                            this.formObj.exec('kpCode', 'clear')
                            this.formObj.exec('jpCode', 'clear')
                        }
                    },
                    col: {
                        span: 6
                    }
                }, {
                    type: 'AutoCompleteQuery',
                    field: 'kpCode',
                    title: '平台名称：',
                    props: {
                        placeholder: '请选择平台或输入名称搜索',
                        fieldValue: 'code',
                        fieldLabel: 'name',
                        value: '',
                        queryFunc: (keyWord) => {
                            const opt = {
                                pageNum: 1,
                                pageSize: 20,
                                kcCode: this.formObj.getValue('kcCode'),
                                _loading: false, //不显示加载中图标
                            }
                            if (keyWord !== '') {
                                opt.keyWord = keyWord;
                            }
                            return getKpList(opt).then(res => {
                                if (res.data) {
                                    const list = res.data.map(item => ({
                                        ...item,
                                        value: item.name,
                                    }))
                                    return list
                                } else {
                                    return []
                                }
                            })
                        }
                    },
                    col: {
                        span: 6
                    }
                }, {
                    type: 'select',
                    field: "glCode",
                    title: '线路名称：',
                    value: '',
                    options: [],
                    on: {
                        change: (item) => {
                            //清空公司 平台 计量点
                            const formData = this.formObj.bind();
                            formData.kcCode = '';
                            this.formObj.exec('kpCode', 'clear')
                            this.formObj.exec('jpCode', 'clear')
                        }
                    },
                    props: {
                        filterable: true,
                    },
                    col: {
                        span: 6,
                    },
                }, {
                    type: 'SubmitBtns',
                    field: 'btn',
                    emit: ['search', 'reset'],
                    col: {
                        span: 6
                    }
                }, {
                    type: 'input',
                    field: 'jpName',
                    title: '计量点名称：',
                    value: '',
                    props: {
                        placeholder: "请输入计量点名称",
                    },

                    col: {
                        span: 6
                    }
                }, {
                    type: 'select',
                    field: "jfrom",
                    title: '资产属性：',
                    value: '',
                    options: [{
                        value: '',
                        label: '全部',
                    }, {
                        value: '自有',
                        label: '自有',
                    }, {
                        value: '国网',
                        label: '国网',
                    }],
                    col: {
                        span: 6
                    }
                }, {
                    type: 'select',
                    field: "status",
                    title: '设备状态：',
                    value: '',
                    options: [{
                        value: '',
                        label: '全部',
                    }, {
                        value: '在运',
                        label: '在运',
                    }, {
                        value: '停运',
                        label: '停运',
                    }],
                    col: {
                        span: 6,

                    }
                }, {
                    type: 'ElCol',
                    props: {
                        span: 6,
                    },
                }, {
                    type: 'input',
                    field: "gcAccount",
                    title: '户　　号：',
                    value: '',
                    props: {
                        placeholder: '请输入户号',
                    },
                    col: {
                        span: 6,
                    }
                }]
            }
        },
        methods: {
            search() {
                const formData = this.formObj.formData();
                const opt = {
                    ...formData
                }
                for (let key in opt) {
                    if (opt[key] == '' || opt[key] == undefined) delete opt[key];
                }
                this.$emit('searchHand', opt);
            },
            //新增
            add() {
                this.$emit('add');
            },
        }
    }
</script>