<template>

    <el-form :model="selectedObj" :rules="rules" ref="form" :class="isEdit?'form':'form onlyShow'" :disabled="!isEdit">
        <div class="li">
            <div class="item">
                <el-form-item prop="content">
                    <div class="label"><span class="important">*</span>变更内容：</div>
                    <el-select v-model="selectedObj.content" placeholder="请选择变更内容" @change="change">
                        <el-option label="基础台账信息变更" value="1"></el-option>
                        <el-option label="设备信息变更" value="2"></el-option>
                    </el-select>
                </el-form-item>
            </div>
        </div>
        <div class="li">
            <div class="item">
                <el-form-item prop="kpCode">
                    <div class="label"><span class="important">*</span>平台：</div>
                    <el-autocomplete ref="autocomplete" v-model="selectedObj.kpName" @focus="autocompleteFocusFlag=true"
                        @blur="autocompleteFocusFlag=false" :fetch-suggestions="querySearchKp" placeholder="请选择平台"
                        @select="item => handleSelectKp(item,'kp')" clearable @clear="clear"></el-autocomplete>
                </el-form-item>
            </div>
        </div>
        <div class="li" v-show="selectedObj.content==1||selectedObj.content==2">
            <div class="item">
                <el-form-item prop="jpCode">
                    <div class="label"><span class="important">*</span>变更计量点：</div>
                    <el-autocomplete v-model="selectedObj.jpName" :fetch-suggestions="querySearchAsync"
                        placeholder="请输入计量点名称" @select="handleSelect"></el-autocomplete>
                </el-form-item>
            </div>
        </div>
        <div class="li">
            <div class="item">
                <el-form-item prop="cause">
                    <div class="label"><span class="important">*</span>变更原因：</div>
                    <el-input v-model="selectedObj.cause" placeholder="请输入变更原因"></el-input>
                </el-form-item>
            </div>
        </div>
        <div class="li">
            <div class="item">
                <el-form-item>
                    <div class="label">变更备注：</div>
                    <el-input class="textarea" type="textarea" v-model="selectedObj.remark" placeholder="变更备注"
                        :autosize="{ minRows: 3, maxRows:10}"></el-input>
                </el-form-item>
            </div>
        </div>

    </el-form>
</template>
<script>
    //接口
    import {
        getPointsList,
        getKpList,
        getKcList,

    } from "@/api/archives.js";
    export default {
        props: {
            formDataChange: { //已经选中的属性
                type: Object,
                default: () => {}
            },
            isEdit: { //是否是编辑状态
                type: Boolean,
                default: false,
            },
            id: String,
        },
        data() {
            return {
                kp: '',
                //选择计量点
                loadProps: {
                    lazy: true,
                    lazyLoad(node, resolve) {
                        const {
                            level
                        } = node;
                        if (level == 0) {
                            getKcList().then(res => {
                                if (res.data) {
                                    const results = res.data.map(item => ({
                                        value: item.code,
                                        label: item.shortName,
                                        leaf: false
                                    }))
                                    resolve(res)
                                } else {
                                    resolve([])
                                }
                            })
                        }
                    }
                },
                rules: {
                    kpCode: [{
                        required: true,
                        message: '平台不能为空',
                        trigger: 'change'
                    }],
                    jpCode: [{
                        required: true,
                        message: '计量点不能为空',
                        trigger: 'change'
                    }],
                    cause: [{
                        required: true,
                        message: '变更原因不能为空',
                        trigger: 'blur'
                    }],
                },

                selectedObj: {
                    kpCode: '', //选择的平台编码
                    kpName: '', //选择的平台
                    jpCode: '', //选择的计量点编码
                    content: '1', //内容
                    cause: '', //变更原因
                    remark: '', //变更备注
                    jpName: '',
                },

                selected: [],
                autocompleteFocusFlag: false,
            }
        },
        methods: {
            handleSelectKp(item, prop) {
                this.selectedObj.kpCode = item.code;
                this.$refs['form'].validateField('kpCode');
            },
            //搜索下拉平台
            querySearchKp(keyWord, cb) {
                const opt = {
                    pageNum: 1,
                    pageSize: 20,
                    _loading: false, //不显示加载中图标
                }
                if (keyWord == '') {
                    this.selectedObj.kpName = '';
                }
                if (keyWord !== '') {
                    opt.keyWord = keyWord;
                }
                getKpList(opt).then(res => {
                    if (res.data) {
                        const list = res.data.map(item => ({
                            ...item,
                            value: item.name,
                        }))
                        cb(list);
                        if (list.length == 0) {
                            this.selectedObj.kpCode = '';
                            this.selectedObj.kpName = '';
                        }
                    } else {
                        cb([]);
                    }
                })
            },
            //搜索函数
            querySearchAsync(keyWord, cb) {
                getPointsList({
                    jpName: keyWord,
                    kpCode: this.selectedObj.kpCode,
                    _loading: false,
                }).then(res => {
                    if (res.list) {
                        const list = res.list.rows.map(item => ({
                            ...item,
                            value: item.name,
                        }))
                        cb(list);
                    } else {
                        cb([]);
                    }
                })
            },
            handleSelect(item) {
                this.selectedObj.jpCode = item.code; //选择的计量点code
                this.$refs['form'].validateField('jpCode');
            },
            changeKp(item) { //平台变化 清空计量点
                this.selectedObj.jpCode = ''; //选择的计量点code
                this.selectedObj.jpName = ''; //选择的计量点code
                this.$refs['form'].validateField('kpCode');
            },
            change(item) {
                if (item == 1 || item == 2) { //需要选择计量点
                    this.rules.jpCode = [{
                        required: true,
                        message: '计量点不能为空',
                        trigger: 'blur'
                    }];
                } else {
                    delete this.rules.jpCode;
                }
            },
            //表单提交验证
            submitForm(success) {
                this.$refs['form'].validate((valid) => {
                    if (valid) {
                        const copy = JSON.parse(JSON.stringify(this.selectedObj))
                        delete copy.kcName;
                        success(copy);
                    } else {
                        return false;
                    }
                });
            },
            reset() {
                var val = {
                    kpCode: '', //选择的平台编码
                    kpName: '', //选择的平台名称
                    jpCode: '', //选择的计量点编码
                    content: '1', //内容
                    cause: '', //变更原因
                    remark: '', //变更备注
                    jpName: '',
                }
                for (let key in val) {
                    this.selectedObj[key] = val[key];
                }
            },
            clear() {
                if (this.autocompleteFocusFlag) this.$refs.autocomplete.activated = true;
            },
        },
        watch: {
            formDataChange: {
                handler(val) {
                    if (!val) return;
                    if (Object.keys(val).length === 0) {
                        this.reset();
                        return;
                    }
                    for (let key in val) {
                        this.selectedObj[key] = val[key];
                    }
                    delete this.selectedObj.ejpCode; //删除新增计量点编码
                    delete this.selectedObj.id; //删除记录id
                },
                deep: true,
                immediate: true,
            },
        }

    }
</script>

<style scoped lang="scss">
    .form {
        padding-left: 15px;

        .li {

            display: flex;
            margin-bottom: 22px;

            .item {
                width: 100%;
                display: flex;
                line-height: 32px;
                height: 32;

                .label {
                    min-width: 92px;
                    margin-right: 2px;
                    color: #333;
                    text-align: right;
                }

                .rt {
                    flex: 1;
                }

                .btns {
                    width: 100%;
                    display: flex;
                    justify-content: flex-end;

                }

                .textarea {
                    width: 270px;
                }
            }
        }

        .important {
            color: #FF2B73;
        }

        /deep/ .item .el-input__inner {
            width: 270px;
            height: 32px;
            line-height: 32px;
        }

        /deep/ .el-form-item {
            margin-bottom: 0;
        }

        /deep/ .el-form-item__content {
            display: flex;
            line-height: 32px;
        }
    }
</style>