<!--图片预览组件-->
<!--
实用方式：  参考

-->

<template>
    <div class="files">
        <div class="item" v-for="file in items">
            <div class="img">
                <img v-if="isImg(file)" :src="file.url" alt="" srcset="">
                <div v-else class="box">
                    <svg-icon :icon-class="getPreType(file)" class-name="logo-img"></svg-icon>
                </div>

                <div class="btns">
                    <i class="el-icon-zoom-in" v-if="isImg(file)" @click="handlePictureCardPreview(file)"></i>
                    <i class="el-icon-download " @click="downloadExcel(file)"></i>
                </div>
            </div>

            <div class="des">
                <el-tooltip :content="file.des" placement="top">
                    <el-button> {{file.des}}</el-button>
                </el-tooltip>
            </div>
        </div>
    </div>
</template>
<script>
    import {
        baseUrl,
        baseProfileUrl,
    } from '@/api/baseUrl.js';
    import {
        resourceUrl,
        getPointDetail,
    } from '@/api/archives.js';

    export default {
        props: {
            formData: Object,
            code: String,
            jpChecked: {
                type: Number,
                default: 1,
            } //计量点是否通过状态
        },
        mounted() {
            this.replay(this.formData)
            this.code && this.getById(this.code)
        },
        data() {
            return {
                items: [{
                    des: '电能表检定合格证1',
                    url: 'http://10.100.10.56/profile/upload/2021/07/06/1625563884680_微信图片_20210603102546.png',
                    name: '微信图片_20210603102546.png',
                }, {
                    des: '电能表检定合格证2',
                    url: 'http://10.100.10.56/profile/upload/2021/07/06/1625563884680_微信图片_20210603102546.png',
                    name: '微信图片_20210603102546.png',
                }], //文件列表
            }
        },
        computed: {
            images() {
                return this.items.filter(_ => this.isImg(_)).map(_ => _.url);
            }
        },
        methods: {
            click(item) {},
            //根据id获取计量点信息
            getById(code) {
                getPointDetail({
                    jpcode: code,
                    checked: this.jpChecked
                }).then(res => {
                    const obj = res.data;
                    if (obj) {
                        this.replay(obj);
                    }
                })
            },
            replay(val) { // 还原文件上传历史
                const {
                    pathDy,
                    pathDl,
                    pathDb,
                    pathDs,
                    pathXc1,
                    pathXc2,
                    pathXc3
                } = val;
                let desArr = ['电能表检定合格证', '现场验收评价表', '电压互感器检定合格证', '电流互感器检定合格证', '现场照片上传'];
                //现场照片 1 2 3
                let xcArr = (pathXc1 && pathXc1.split) ? pathXc1.split(',') : [];
                if (pathXc2) xcArr.push(pathXc2)
                if (pathXc3) xcArr.push(pathXc3)
                this.items = [pathDb, pathDs, pathDy, pathDl].concat(xcArr).map((item,
                    index) => ({
                    des: desArr[index >= 4 ? 4 : index],
                    url: this.getShowUrl(item),
                    name: this._getFileNameByUrl(item),
                })).filter(_ => _.name)
            },
            //获取完整绝对文件地址
            getShowUrl(url) {
                if (!url) return '';
                return baseProfileUrl + url;
            },
            //根据文件名获取文件名称
            _getFileNameByUrl(url) {
                if (!url) return '';
                const _index = url.indexOf('_');
                return _index !== -1 ? url.slice(_index + 1) : url;
            },
            isImg(file) {
                const name = file.name.toLowerCase();
                return ['.png', '.jpeg', '.jpg'].find(item => {
                    return name.indexOf(item) !== -1;
                })
            },
            //获取svg文件名
            getPreType(file) {
                const name = file.name.toLowerCase();
                const baseImg = ''
                if (name.indexOf('.doc') !== -1 || name.indexOf('.docx') !== -1) { // word
                    return 'w'
                } else if (name.indexOf('.pdf') !== -1) {
                    return 'pdf'
                } else if (name.indexOf('.ppt') !== -1 || name.indexOf('.pptx') !== -1) {
                    return 'ppt'
                } else if (name.indexOf('.xls') !== -1 || name.indexOf('.xlsx') !== -1) {
                    return 's'
                }
                return 'zip';
            },
            //预览图片
            handlePictureCardPreview(item) {
                console.log(item.url);
                const defaultIndex = this.images.indexOf(item.url);
                this.$viewerApi({
                    images: this.images,
                    options: {
                        initialViewIndex: defaultIndex,
                    }
                })
            },
            //下载文件
            downloadExcel(item) {
                const url = item.url;
                const fileName = item.name;
                const a = document.createElement('a');
                a.setAttribute('download', fileName);
                a.setAttribute('href', resourceUrl + "?resource=" + url.replace(baseProfileUrl, ''));
                a.click();
            }
        },

        watch: {
            formData: {
                handler(val) {
                    if (!val) return;
                    this.replay(val)
                },
                deep: true,
                immediate: true,
            },
            code: {
                handler(val) {
                    if (!val) return;
                    this.getById(val);
                },
                immediate: true,
            },
        }
    };
</script>
<style scoped lang="scss">
    .files {
        display: flex;
        flex-wrap: wrap;

        .logo-img {
            width: 75px;
            height: 75px;
            padding: 20px;
            box-sizing: border-box;
        }

        .item {

            .box {
                width: 75px;
                height: 75px;
                margin: 10px;
                box-sizing: border-box;
                background-color: #F4F4F4;
            }

            display: flex;
            justify-content: center;
            flex-direction: column;
            margin-right: 10px;
            margin-bottom: 16px;

            img {
                width: calc(100% - 20px);
                height: calc(100% - 20px);
                margin: 10px;
                box-sizing: border-box;
            }

            .img {
                width: 95px;
                height: 95px;
                border-radius: 2px;
                background-color: #fff;
                border: 1px solid #c0ccda;
                border-radius: 2px;
                box-sizing: border-box;

                margin: 0;
                display: inline-block;
                position: relative;

                .btns {
                    position: absolute;
                    left: 0;
                    top: 0;
                    right: 0;
                    bottom: 0;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    transition: all 0.3s;

                    &:hover {
                        background-color: rgba(0, 0, 0, 0.5);

                        i {
                            display: block;
                        }
                    }


                    i {
                        display: none;
                        font-size: 20px;
                        color: #fff;
                        cursor: pointer;
                        margin-left: 10px;
                        margin-right: 10px;
                    }
                }
            }

            .des {
                border-radius: 2px;
                border: 1px solid #DCDCDC;
                text-align: center;
                padding: 3px 8px;
                margin-top: 10px;
                font-size: 12px;
                text-align: center;
                box-sizing: border-box;
                width: 95px;

                /deep/ span {
                    display: block;
                    width: 80px;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    white-space: nowrap;
                }

                /deep/ .el-button {
                    padding: 0;
                    font-size: 12px;
                    height: 20px;
                    border: 0;
                }

                /deep/.el-button:focus,
                /deep/.el-button:hover {
                    color: #2E4AF7;
                    border-color: transparent;
                    background-color: transparent;
                }
            }
        }
    }
</style>