<template>
    <div class="upload-box">
        <div class="item">
            <div class="title">电能表检定合格证</div>
            <div class="files">
                <div class="label">附件上传：</div>
                <div class="boxs">
                    <el-upload :class="{hide:pathDb.length}" :action="fileUploadUrl" :headers="headers"
                        :on-success="handleChangeTwo" :before-upload="beforeUploadPublic" list-type="picture-card"
                        :limit="1" :file-list="pathDb">
                        <i slot="default" class="el-icon-plus"></i>
                        <div slot="file" slot-scope="{file}">
                            <img v-if="isImg(file)" class="el-upload-list__item-thumbnail" :src="file.url" alt="">
                            <div v-else class="box">
                                <svg-icon :icon-class="getPreType(file)" class-name="logo-img"></svg-icon>
                            </div>
                            <span class="el-upload-list__item-actions">
                                <span v-if="isImg(file)" class="el-upload-list__item-preview"
                                    @click="handlePictureCardPreview(file)">
                                    <i class="el-icon-zoom-in"></i>
                                </span>
                                <span class="el-upload-list__item-delete" @click="handleRemove(pathDb,file)">
                                    <i class="el-icon-delete"></i>
                                </span>
                            </span>
                        </div>
                    </el-upload>
                </div>
            </div>
        </div>
        <div class="item">
            <div class="title">现场验收评价表</div>
            <div class="files">
                <div class="label">附件上传：</div>
                <div class="boxs">
                    <el-upload :class="{hide:pathDs.length}" :action="fileUploadUrl" :headers="headers"
                        :on-success="handleChangeFour" :before-upload="beforeUploadPublic" list-type="picture-card"
                        :limit="1" :file-list="pathDs">
                        <i slot="default" class="el-icon-plus"></i>
                        <div slot="file" slot-scope="{file}">
                            <img v-if="isImg(file)" class="el-upload-list__item-thumbnail" :src="file.url" alt="">
                            <div v-else class="box">
                                <svg-icon :icon-class="getPreType(file)" class-name="logo-img"></svg-icon>
                            </div>
                            <span class="el-upload-list__item-actions">
                                <span v-if="isImg(file)" class="el-upload-list__item-preview"
                                    @click="handlePictureCardPreview(file)">
                                    <i class="el-icon-zoom-in"></i>
                                </span>
                                <span class="el-upload-list__item-delete" @click="handleRemove(pathDs,file)">
                                    <i class="el-icon-delete"></i>
                                </span>
                            </span>
                        </div>
                    </el-upload>
                </div>
            </div>
        </div>
        <div class="item">
            <div class="title">电压互感器检定合格证</div>
            <div class="files">
                <div class="label">附件上传：</div>
                <div class="boxs">
                    <el-upload :class="{hide:pathDy.length}" :action="fileUploadUrl" :headers="headers"
                        :on-success="handleChangeOne" :before-upload="beforeUploadPublic" list-type="picture-card"
                        :limit="1" :file-list="pathDy">
                        <i slot="default" class="el-icon-plus"></i>
                        <div slot="file" slot-scope="{file}">
                            <img v-if="isImg(file)" class="el-upload-list__item-thumbnail" :src="file.url" alt="">
                            <div v-else class="box">
                                <svg-icon :icon-class="getPreType(file)" class-name="logo-img"></svg-icon>
                            </div>
                            <span class="el-upload-list__item-actions">
                                <span v-if="isImg(file)" class="el-upload-list__item-preview"
                                    @click="handlePictureCardPreview(file)">
                                    <i class="el-icon-zoom-in"></i>
                                </span>
                                <span class="el-upload-list__item-delete" @click="handleRemove(pathDy,file)">
                                    <i class="el-icon-delete"></i>
                                </span>
                            </span>
                        </div>
                    </el-upload>
                </div>
            </div>
        </div>

        <div class="item">
            <div class="title">电流互感器检定合格证</div>
            <div class="files">
                <div class="label">附件上传：</div>
                <div class="boxs">
                    <el-upload :class="{hide:pathDl.length}" :action="fileUploadUrl" :headers="headers"
                        :on-success="handleChangeThree" :before-upload="beforeUploadPublic" list-type="picture-card"
                        :limit="1" :file-list="pathDl">
                        <i slot="default" class="el-icon-plus"></i>
                        <div slot="file" slot-scope="{file}">
                            <img v-if="isImg(file)" class="el-upload-list__item-thumbnail" :src="file.url" alt="">
                            <div v-else class="box">
                                <svg-icon :icon-class="getPreType(file)" class-name="logo-img"></svg-icon>
                            </div>
                            <span class="el-upload-list__item-actions">
                                <span v-if="isImg(file)" class="el-upload-list__item-preview"
                                    @click="handlePictureCardPreview(file)">
                                    <i class="el-icon-zoom-in"></i>
                                </span>
                                <span class="el-upload-list__item-delete" @click="handleRemove(pathDl,file)">
                                    <i class="el-icon-delete"></i>
                                </span>
                            </span>
                        </div>
                    </el-upload>
                </div>
            </div>
        </div>

        <div class="item allLine">
            <div class="title">现场照片上传 </div>
            <div class="files">
                <div class="label">附件上传：</div>
                <div class="boxs">
                    <el-upload :class="{hide:fileList_pic.length>=5}" :action="fileUploadUrl" :headers="headers"
                        :on-success="handleChangeFive" :before-upload="beforeAvatarUpload" list-type="picture-card"
                        :limit="5" :file-list="fileList_pic">
                        <i slot="default" class="el-icon-plus"></i>
                        <div slot="file" slot-scope="{file}">
                            <img v-if="isImg(file)" class="el-upload-list__item-thumbnail" :src="file.url" alt="">
                            <div v-else class="box">
                                <svg-icon :icon-class="getPreType(file)" class-name="logo-img"></svg-icon>
                            </div>
                            <span class="el-upload-list__item-actions">
                                <span v-if="isImg(file)" class="el-upload-list__item-preview"
                                    @click="handlePictureCardPreview(file)">
                                    <i class="el-icon-zoom-in"></i>
                                </span>
                                <span class="el-upload-list__item-delete" @click="handleRemove(fileList_pic,file)">
                                    <i class="el-icon-delete"></i>
                                </span>
                            </span>
                        </div>
                    </el-upload>
                </div>
            </div>
        </div>
        <div class="item allLine noBottom">
            <UploadProgress ref="uploadPreogress" />
        </div>
    </div>
</template>
<script>
    import {
        fileUploadUrl,
        getPointDetail,
    } from '@/api/archives';
    import {
        getToken
    } from '@/utils/auth'
    import {
        Message
    } from 'element-ui';
    import tool from '@/utils/tool'
    import {
        baseUrl,
        baseProfileUrl
    } from '@/api/baseUrl.js';
    import UploadProgress from "@/components/UploadProgress.vue"; //进度条
    export default {
        components: {
            UploadProgress,
        },
        props: {
            formData: Object,
            isEdit: { //是否是编辑状态
                type: Boolean,
                default: false,
            },
            code: { //是否是编辑状态 
                type: String,
                default: '',
            },
            jpChecked: {
                type: Number,
                default: 1,
            } //计量点是否通过状态
        },
        data() {
            return {
                fileUploadUrl, //上传文件地址
                headers: {
                    uapAccessToken: getToken('accessToken'),
                    uapRefreshToken: getToken('refreshToken'),
                },
                pathDy: [], //电压互感器
                pathDl: [], //电流互感器
                pathDb: [], // 电能表合格证
                pathDs: [], // 现场验收评价表
                fileList_pic: [], // 现场照片
            }
        },
        computed: {
            images() {
                let dbArr = this.pathDb.filter(_ => this.isImg(_)).map(_ => _.url);
                let dyArr = this.pathDy.filter(_ => this.isImg(_)).map(_ => _.url);
                let dlArr = this.pathDl.filter(_ => this.isImg(_)).map(_ => _.url);
                let dsArr = this.pathDs.filter(_ => this.isImg(_)).map(_ => _.url);
                let picArr = this.fileList_pic.filter(_ => this.isImg(_)).map(_ => _.url);
                return [...dbArr, ...dyArr, ...dlArr, ...dsArr, ...picArr]
            }
        },
        mounted() {
            this.replay(this.formData)
        },
        methods: {
            isImg(file) {
                const name = file.name.toLowerCase();
                return ['.png', '.jpeg', '.jpg'].find(item => {
                    return name.indexOf(item) !== -1;
                })
            },
            //获取svg文件名
            getPreType(file) {
                const name = file.name.toLowerCase();
                const baseImg = ''
                if (name.indexOf('.doc') !== -1 || name.indexOf('.docx') !== -1) { // word
                    return 'w'
                } else if (name.indexOf('.pdf') !== -1) {
                    return 'pdf'
                } else if (name.indexOf('.ppt') !== -1 || name.indexOf('.pptx') !== -1) {
                    return 'ppt'
                } else if (name.indexOf('.xls') !== -1 || name.indexOf('.xlsx') !== -1) {
                    return 's'
                }
                return 'zip';
            },
            handleAvatarSuccess(res, file) {
                if (res.code !== 200) {
                    Message.error({
                        message: '上传失败' + res.msg,
                    })
                    return;
                }
            },
            //删除图片
            handleRemove(data, file) {
                let index = 0;
                data.map((item, index) => {
                    if (item.name === file.name) {
                        index = index;
                    }
                })
                data.splice(index, 1)
            },
            //预览图片
            handlePictureCardPreview(file) {
                console.log(this.images);
                const defaultIndex = this.images.indexOf(file.url);
                this.$viewerApi({
                    images: this.images,
                    options: {
                        initialViewIndex: defaultIndex,
                    }
                })
            },
            //电压互感器
            handleChangeOne(res, file, fileList) {
                if (res.code == 200) {
                    this.pathDy = fileList.slice(0);
                    this.$refs.uploadPreogress.success();
                } else {
                    this.msg(res.msg);
                    this.pathDy = [];
                }
            },
            //电流互感器
            handleChangeTwo(res, file, fileList) {
                if (res.code == 200) {
                    this.pathDb = fileList.slice(0);
                    this.$refs.uploadPreogress.success();
                } else {
                    this.msg(res.msg);
                    this.pathDb = [];
                }
            },
            //电流互感器
            handleChangeThree(res, file, fileList) {
                if (res.code == 200) {
                    this.pathDl = fileList.slice(0);
                    this.$refs.uploadPreogress.success();
                } else {
                    this.msg(res.msg);
                    this.pathDl = [];
                }
            },
            //现场验收评价表
            handleChangeFour(res, file, fileList) {
                if (res.code == 200) {
                    this.pathDs = fileList.slice(0);
                    this.$refs.uploadPreogress.success();
                } else {
                    this.msg(res.msg);
                    this.pathDs = [];
                }
            },
            //互感器
            handleChangeFive(res, file, fileList) {
                if (res.code == 200) {
                    this.fileList_pic = fileList.slice(0);
                    this.$refs.uploadPreogress.success();
                } else {
                    this.msg(res.msg);
                    this.fileList_pic = [];
                }
            },
            //上传验证是否已经正在上传,如果是图片压缩
            beforeUploadPublic(file) {
                if (this.$refs.uploadPreogress.isLoading()) return false;
                this.$refs.uploadPreogress.start(); //开始增加进度
                const name = file.name.toLowerCase();
                const isPic = ['.png', '.jpeg', '.jpg'].find(item => {
                    return name.indexOf(item) !== -1;
                })
                const isLt2M = file.size / 1024 / 1024 < 2;
                if (!isPic || isLt2M) {
                    return true;
                }
                return tool.compressImg(file);
            },
            beforeAvatarUpload(file) {
                if (this.$refs.uploadPreogress.isLoading()) return false;
                const name = file.name.toLowerCase();
                const isPic = ['.png', '.jpeg', '.jpg'].find(item => {
                    return name.indexOf(item) !== -1;
                })
                const isLt2M = file.size / 1024 / 1024 < 2;
                if (!isPic) {
                    this.$message.error('上传图片格式不正确!');
                    return false;
                }
                this.$refs.uploadPreogress.start(); //开始增加进度
                //如果时图片且超过2M 压缩图片
                if (!isLt2M) {
                    return tool.compressImg(file);
                } else {
                    return true;
                }
            },
            //提交
            submitForm(success) {
                if (this.$refs.uploadPreogress.isLoading()) { //正在上传图片
                    this.$message.warning('正在上传文件，请稍后再试');
                    return;
                }
                const data = {
                    pathDy: this._getUrl(this.pathDy[0]),
                    pathDl: this._getUrl(this.pathDl[0]),
                    pathDb: this._getUrl(this.pathDb[0]),
                    pathDs: this._getUrl(this.pathDs[0]),
                    pathXc1: this.fileList_pic.map(img => this._getUrl(img)).join(',')
                    // pathXc2: this._getUrl(this.fileList_pic[1]),
                    // pathXc3: this._getUrl(this.fileList_pic[2]),
                }
                success(data);
            },
            //根据文件名获取文件名称
            _getFileNameByUrl(url) {
                if (!url) return '';
                const _index = url.indexOf('_');
                return _index !== -1 ? url.slice(_index + 1) : url;
            },
            //获取保存的url地址
            _getUrl(item) {
                if (!item) return '';
                if (item.response) {
                    return item.response.fileName;
                } else if (item.url) {
                    return item.url.replace(baseProfileUrl, '');
                }
                return '';
            },
            //获取完整绝对文件地址
            getShowUrl(url) {
                if (!url) return '';
                return baseProfileUrl + url;
            },
            msg(err) {
                this.$message.error('上传出错:' + err);
            },
            replay(val) { // 还原文件上传历史
                if (!val) return this.reset();
                const {
                    pathDy,
                    pathDl,
                    pathDb,
                    pathDs,
                    pathXc1,
                    pathXc2,
                    pathXc3
                } = val;
                //复盘
                this.pathDy = [{ //电压互感器
                    name: this._getFileNameByUrl(pathDy),
                    url: this.getShowUrl(pathDy),
                }].filter(_ => _.url);
                this.pathDl = [{ //电流互感器
                    name: this._getFileNameByUrl(pathDl),
                    url: this.getShowUrl(pathDl),
                }].filter(_ => _.url);
                this.pathDb = [{ // 电能表合格证
                    name: this._getFileNameByUrl(pathDb),
                    url: this.getShowUrl(pathDb),
                }].filter(_ => _.url);
                this.pathDs = [{ // 现场验收评价表
                    name: this._getFileNameByUrl(pathDs),
                    url: this.getShowUrl(pathDs),
                }].filter(_ => _.url);
                //现场照片 1 2 3
                let xcArr = (pathXc1 && pathXc1.split) ? pathXc1.split(',') : [];
                if (pathXc2) xcArr.push(pathXc2)
                if (pathXc3) xcArr.push(pathXc3)
                this.fileList_pic = xcArr.map(item => ({
                    name: this._getFileNameByUrl(item),
                    url: this.getShowUrl(item),
                })).filter(_ => _.name)
                this.$refs.uploadPreogress && this.$refs.uploadPreogress.init();
            },
            reset() {
                this.pathDy = []; //电压互感器
                this.pathDl = []; //电流互感器
                this.pathDb = []; // 电能表合格证
                this.pathDs = []; // 现场验收评价表
                this.fileList_pic = []; // 现场照片
            },
            getById(code) {
                getPointDetail({
                    jpcode: code,
                    checked: this.jpChecked
                }).then(res => {
                    this.replay(res.data)
                })
            },
        },
        watch: {
            formData: {
                handler(val, oldVal) {
                    //复盘文件
                    if (!val) return;
                    this.replay(val)
                    if (Object.keys(val).length === 0) {
                        this.reset();
                        return;
                    }
                },
                deep: true //深度监听
            },
            code: {
                handler(val) {
                    if (!val) return;
                    this.getById(val);
                },
                immediate: true,
            },
        }
    }
</script>

<style scoped lang="scss">
    .upload-box {
        width: 100%;
        display: flex;
        flex-flow: row wrap;

        align-content: flex-start;

        .logo-img {
            width: 75px;
            height: 75px;
            line-height: 0;
            padding: 20px;
            box-sizing: border-box;
        }

        .noBottom {
            padding-bottom: 0 !important;
        }

        .item {
            &.allLine {
                flex: 0 0 100%;
            }

            .box {
                width: 75px;
                height: 75px;
                margin: 10px;
                box-sizing: border-box;
                background-color: #F4F4F4;
            }

            flex: 0 0 50%;
            padding-bottom: 24px;

            .title {
                line-height: 20px;
                color: #000;
                font-weight: 600;
                margin-bottom: 24px;
            }

            .notice {
                color: #999999;
                font-size: 12px;
            }

            .files {
                display: flex;

                flex: 1;

                .label {
                    width: 70px;
                }
            }
        }

        .prev-box {
            height: 104px;
            line-height: 30px;
            font-size: 12px;
            padding: 10px;
            color: #2E4AF7;
        }

        /deep/ .hide .el-upload--picture-card {
            display: none;
        }
    }
</style>