<!--计量点表格-->

<template>
    <div style="width: 100%" class="cus-table">
        <el-table v-table-scroll style="width: 100%" :data="tableData" :row-class-name="tableRowClassName"
            :header-cell-style="{background:'#E6EAFE',color:'#606266'}">
            <el-table-column type="index" label="序号" width="90" />
            <el-table-column label="所属公司" prop="kcName" show-overflow-tooltip></el-table-column>
            <el-table-column label="所属平台" prop="kpName" width="150" show-overflow-tooltip></el-table-column>
            <el-table-column label="所属路线" prop="glName" width="200" show-overflow-tooltip></el-table-column>
            <el-table-column label="计量点名称" prop="name" width="240" show-overflow-tooltip></el-table-column>
            <el-table-column label="计量方式" prop="jlTy" width="120">
                <template slot-scope="scope">
                    <span>{{jlType[scope.row['jlTy']] }}</span>
                </template>
            </el-table-column>
            <el-table-column label="资产属性" prop="jfrom" width="80">
                <template slot-scope="scope">
                    <span>{{scope.row['jfrom'] }}</span>
                </template>
            </el-table-column>
            <el-table-column label="设备状态" prop="status" width="80">
                <template slot-scope="scope">
                    <span :class="scope.row['status']==='在运'?'green':'red'">{{scope.row['status'] }}</span>
                </template>
            </el-table-column>
            <el-table-column label="状态" prop="checkStatus" width="120">
                <template slot-scope="scope">
                    <span
                        :class="scope.row['checkStatus']==='1'?'green':'red'">{{statusArr[scope.row['checkStatus']!==null?scope.row['checkStatus']:0]}}</span>
                </template>
            </el-table-column>
            <el-table-column label="操作" align="left" fixed="right" class-name="cusTable" width="90">
                <template slot-scope="scope">
                    <span v-if="scope.row.checkStatus==3||scope.row.checkStatus==4" class="cus-btn"
                        @click="handClick(getLabel(scope.row),scope.row)" type="text" size="small">
                        {{getLabel(scope.row)}}
                    </span>
                    <span v-else-if="scope.row.checkStatus==1" class="cus-btn" @click="handClick('计量点信息',scope.row)"
                        type="text" size="small">查看</span>
                    <el-dropdown v-else @command="handleCommand($event,scope.row)">
                        <span class="el-dropdown-link cus-btn">
                            操作<i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <!-- <el-dropdown-item command="采集终端信息">采集终端信息</el-dropdown-item> -->
                            <el-dropdown-item command="计量点信息">计量点信息</el-dropdown-item>
                            <el-dropdown-item v-if="checkBtn" command="审核">审核</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </template>
            </el-table-column>
        </el-table>
        <div class="page pagination-box">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[10, 20, 50, 100]" :page-size="pageSize" layout="prev, pager, next, jumper,total, sizes"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>
<script>
    import {
        mapState
    } from 'vuex'
    import Table from "@/components/ElTableCommon.vue";
    export default {
        extends: Table,
        name: 'table1',
        props: {
            checkBtn: { //是否显示审核功能
                type: Boolean,
                default: false
            }
        },
        data() {
            return {
                jlType: ['', '高供高计', '高供低计', '低供低计'],
                statusArr: ['未审核', '已审核', '', '待上传资料', '待发起审批'],
                replayId: '',
            }
        },
        computed: {
            ...mapState({ //审核人员
                userId: state => state.user.userId, //当前登录的user
            })
        },
        methods: {
            getLabel(row) { //获取操作按钮名称 可能是函数则执行结果返回
                if (row.checkStatus === '3') {
                    return '去上传';
                } else if (row.checkStatus === '4') {
                    return '去发起';
                }
            }
        }
    }
</script>
<style scoped lang="scss">
    .page {
        display: flex;
        justify-content: flex-end;
    }

    .cus-btn {
        color: #2E4AF7;
        font-size: 14px;
        padding: 0 2px;
        cursor: pointer;
    }

    /deep/ .active-row {
        background-color: #F5F7FA;
    }
</style>