<template>
    <div class="overview cus">
        <Steps :items="['计量点信息录入', '资料上传', '发起审核']" :active="stepActive" @click="stepClick"></Steps>
        <SearchInputs @downHand="downHand" @searchHand="searchHand" @add="addHand" :showAddBtn="true"></SearchInputs>
        <CusTable ref="table" :tableData="tableData" @btnClick="btnClick" @handleSizeChange="handleSizeChange"
            @handleCurrentChange="handleCurrentChange" :pageNum="page.pageNum" :pageSize="page.pageSize" :total="total"
            :checkBtn="roles_checkPerson"></CusTable>
        <!--计量点信息录入-->
        <el-dialog title="计量点信息录入" custom-class="cusDialog" :visible.sync="this.dialog==1" width="9rem"
            :before-close="handleClose">
            <div class="content-title">基本信息</div>
            <CusForm ref="CusForm" :isEdit="true" :code="pointCode" :formData="formDetailData" :jpChecked="0">
            </CusForm>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" :loading="loading" @click="submit">提 交</el-button>
                <el-button @click="dialog = 0">关 闭</el-button>
            </span>
        </el-dialog>
        <!--资料上传-->
        <el-dialog title="资料上传" custom-class="cusDialog" :visible.sync="this.dialog==2" width="7rem"
            :before-close="handleClose">
            <Upload ref="upload" :formData="formDetailData"></Upload>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" :loading="loading" @click="submit">提 交</el-button>
                <el-button @click="dialog = 0">关 闭</el-button>
            </span>
        </el-dialog>
        <!--发起审核-->
        <SelectUser :show="this.dialog==3" @selectPeople="selectPeople"></SelectUser>
        <!--查看计量点信息框-->
        <el-dialog title="计量点信息" custom-class="cusDialog" :visible.sync="dialogVisible" width="8rem"
            :before-close="handleCloseDetail">
            <div class="content-title">基本信息</div>
            <CusForm1 :formData="showFormDetailData" :isEdit="false"></CusForm1>
            <div class="content-title">附件信息</div>
            <FilesList :formData="showFormDetailData"></FilesList>
            <span slot="footer" class="dialog-footer">
                <el-button @click="handleCloseDetail">关 闭</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    import TableCommon from "@/components/TableCommon.vue";
    import FilesList from "@/components/FilesList.vue";
    import CusTable from "./components/CusTable.vue";
    import Steps from "@/components/Steps.vue";
    import SearchInputs from "./components/SearchInputs.vue";
    const CusForm = () => import('./components/CusForm.vue')
    const CusForm1 = () => import('./components/CusForm.vue')
    import Upload from "./components/Upload.vue";
    import SelectUser from "./components/SelectUser.vue";
    //接口
    import {
        postAddClient,
        postUpdateClient,
        postAddPoint,
        postUpdatePoint,
        doCheckPoint,
        exportRecord,
        getJpointlistTemp
    } from "@/api/archives.js";
    //公用函数
    import tool from "@/utils/tool.js";
    import {
        parseTime
    } from "@/utils/index.js";
    import {
        ROLENAME
    } from "@/utils/config.js";
    import {
        mapState
    } from 'vuex'
    export default {
        extends: TableCommon,
        name: 'overview',
        components: {
            CusTable, //表格
            Steps, //步骤条
            SearchInputs, //搜索框
            CusForm, //计量点信息录入
            CusForm1: CusForm1, //计量点信息录入
            Upload, //资料上传
            SelectUser, //资料上传
            FilesList, //文件查看
        },
        data() {
            return {
                dialogVisible: false, //弹框
                formDetailData: {}, //每个计量点的详细数据 用于弹框展示
                showFormDetailData: {}, //详细数据用于弹框展示

                stepActive: 0, //当前步骤条状态
                dialog: 0, // 当前弹框类型 1 终端录入 2 计量点录入 3资料上传 4发起审核
                pointCode: '', //新增后的计量点code
            }
        },
        computed: {
            ...mapState({ //是否是审核人员
                roles_checkPerson: state => state.permission.roles.includes(ROLENAME.checkPerson),
                userId: state => state.user.userId, //当前登录的user
            })
        },
        methods: {
            _getData(params = {}) { // 通过接口 获取最新数据 如果无接口 则是第一次加载
                params = {
                    ...this.page,
                    ...this.search
                }
                getJpointlistTemp(params).then(res => {
                    const data = res.list ? res.list.rows : [];
                    this.tableData = data;
                    this.total = res.list.total;
                })
            },
            //步骤条点击
            stepClick(item) {
                switch (item) {
                    case '计量点信息录入':
                        this.dialog = 1;
                        break;
                    case '资料上传':
                        this.dialog = 2;
                        break;
                    case '发起审核':
                        this.dialog = 3;
                        break;
                }
            },
            //操作按钮点击的事件
            btnClick(name, row) {
                if (name == '审核') {
                    this.toExam(row);
                    return;
                }
                if (name == '计量点信息') {
                    this.dialogVisible = true;
                    this.showFormDetailData = row;
                } else if (name == '去上传') {
                    this.formDetailData = row;
                    this.pointCode = row.code;
                    this.stepActive = 1;
                    this.dialog = 2;
                } else if (name == '去发起') {
                    this.formDetailData = row;
                    this.pointCode = row.code;
                    this.stepActive = 2;
                    this.dialog = 3;
                }
            },
            //审核计量点
            toExam(row) {
                this.$confirm('请选择审核结果', '信息审核', {
                    confirmButtonText: '通过',
                    cancelButtonText: '不通过',
                    distinguishCancelAndClose: true,
                    type: 'warning'
                }).then(() => {
                    doCheckPoint({
                        userId: this.userId,
                        jpCode: row.code,
                    }).then(res => {
                        this.$message({
                            type: 'success',
                            message: '操作成功!'
                        });
                        this.reloadTable()
                    })
                }).catch(action => { //不通过
                    if (action == 'close') return;
                    const opt = {
                        ...row
                    }
                    opt.checkStatus = '3'; //审核不通过状态修改为待提交计量点信息
                    this._updatePoint(opt, false).then(flag => {
                        if (flag) {
                            this.reloadTable()
                        }
                    }); //修改计量点提交
                });
            },
            //导出按钮
            downHand() {
                exportRecord({
                    datatype: 'new',
                    ...this.search,
                }).then(tool.down)
            },
            // 新增
            addHand() {
                //本来就是第一步
                if (this.stepActive == 0) {
                    this.dialog = 1; //打开新增框
                } else {
                    this.clearFrom(); //清空步骤条数据
                    this.oldFormDetailData = '';
                    this.dialog = 1; //打开新增框
                    this.stepActive = 0; // 步骤还原
                    this.$refs['table'].clearActiveRow(); //清空table高亮行
                }
            },
            //弹框提交按钮
            submit(data) {
                if (this.dialog === 1) { //计量点信息
                    this.$refs['CusForm'].submitForm((data) => {
                        this.loading = true; //加载
                        this.formDetailData = { //记录计量点信息 以后面提交修改计量点
                            ...data
                        };
                        if (data.code) {
                            if (this.formDetailData.checkStatus <= 3) {
                                data.checkStatus = '3'; //记录为待上传资料状态 用于复盘和显示
                            }
                            this._updatePoint(data).then(flag => {
                                if (flag) {
                                    if (this.stepActive === 0) this.stepActive = 1;
                                }
                            }); //修改计量点提交
                        } else {
                            data.checkStatus = '3'; //记录为待上传资料状态 用于复盘和显示
                            //新增计量点
                            postAddPoint(data).then(res => {
                                this.pointCode = res.data.code;
                                this.formDetailData.checkStatus = res.data.checkStatus;
                                this._nextStep(); // 跳到下一步骤
                                this.loading = false; //加载
                                if (this.stepActive === 0) this.stepActive = 1;
                            }).catch(err => {
                                this.loading = false; //加载
                            })
                        }
                    })
                } else if (this.dialog === 2) { //文件上传
                    this.$refs['upload'].submitForm((data) => {
                        this.loading = true; //加载
                        data.code = this.pointCode;
                        data.checkStatus = '4'; //记录为待发起审批状态
                        //修改计量点信息 加入文件
                        this._updatePoint(data).then((flag) => {
                            //成功才跳转步骤
                            if (flag) {
                                if (this.stepActive === 1) this.stepActive = 2;
                            }
                        });
                    })
                }
            },
            //发起审核
            selectPeople(user) {
                this.dialog = 0;
                if (!user) { //取消选择审批人
                    return;
                }
                const opt = {
                    checkPerson: user.toString(),
                    code: this.pointCode,
                    checkStatus: '0',
                }
                this._updatePoint(opt, false).then((flag) => {
                    if (flag) {
                        this.clearFrom(); // 清空弹框内容
                        //当前步骤增加
                        this.stepActive = 0; //步骤跳转到第一步
                    }
                });
            },
            //修改计量点公共方法
            _updatePoint(opt, stepAdd) {
                // 赋值整合参数
                const copy = {
                    ...this.formDetailData,
                    ...opt,
                }
                //修改计量点审批人
                return postUpdatePoint(copy).then(res => {
                    this.loading = false; //加载
                    if (res.data) {
                        this.loading = false; //加载
                        this._nextStep(stepAdd); // 跳到下一步骤
                        //更新保存的对象记录
                        for (let key in opt) {
                            this.formDetailData[key] = opt[key];
                        }
                        return true;
                    } else {
                        return false
                    }
                }).catch(err => {
                    this.loading = false; //加载
                    return false
                })
            },
            //关闭步骤的弹框
            handleClose(data) {
                this.dialog = 0;
            },
            //关闭查看信息框
            handleCloseDetail(data) {
                this.dialogVisible = false;
                this.clearFrom(true); //清空展示弹框的数据
            },

            //延迟跳转到下一个步骤弹框
            _delayAddDialog(delay = 1000) {
                const old = this.dialog;
                this.dialog = 0;
                if (old <= 2) {
                    setTimeout(() => {
                        this.dialog = old + 1;
                    }, delay)
                }
            },
            //跳转到下一步提示并打开下一个弹框
            /**
             * @stepAdd {boolean} 是否弹出下一个框
             */
            _nextStep(stepAdd = true) {
                this.$notify({
                    title: '成功',
                    message: stepAdd ? '提交成功，即将跳转到下一步骤' : '提交成功',
                    type: 'success',
                    duration: 3000,
                });
                this.loading = false;
                stepAdd && this._delayAddDialog(); //下一步弹框显示
                this.reloadTable(); // 刷新表格数据
            },
            //清空弹框内容
            clearFrom(isShow = 0) { //isshow是否是展示弹框 展示弹框不清空进度条内容
                if (isShow) {
                    this.showFormDetailData = {};
                    return;
                }
                this.stepActive = 0; //步骤跳转到第一步
                this.pointCode = '';
                this.formDetailData = {};
            },
        }
    }
</script>
<style scoped lang="scss">
    .overview {
        margin-top: 0.24rem;

        .search {
            padding: 16px 0 0px 0;

            .li {
                &:last-child {
                    margin-bottom: 0;
                }

                display: flex;
                margin-bottom: 16px;

                .item {
                    width: 25%;
                    display: flex;
                    line-height: 32px;
                    height: 32;

                    .label {
                        min-width: 70px;
                        margin-right: 2px;
                    }

                    .rt {
                        flex: 1;
                    }

                    .btns {
                        width: 100%;
                        display: flex;
                        justify-content: flex-end;
                    }
                }
            }
        }

        /deep/ .item .el-input__inner {
            width: 270px;
            height: 32px;
            line-height: 32px;
        }
    }
</style>