<template>
    <div class="overview cus">
        <InfBox :items="infoIems" title="计量点明细"></InfBox>
        <SearchInputs @downHand="downHand" @searchHand="searchHand"></SearchInputs>
        <CusTable :tableData="tableData" @btnClick="btnClick" @handleSizeChange="handleSizeChange"
            @handleCurrentChange="handleCurrentChange" :pageNum="page.pageNum" :pageSize="page.pageSize" :total="total">
        </CusTable>
        <el-dialog title="计量点信息" custom-class="cusDialog" :visible.sync="dialogVisible" width="9rem"
            :before-close="handleClose">
            <div class="content-title">基本信息</div>
            <CusForm ref="CusForm" :formData="formData" :isEdit="false"></CusForm>
            <div class="content-title">附件信息</div>
            <FilesList :formData="formData"></FilesList>
            <span slot="footer" class="dialog-footer">
                <!-- <el-button v-show="showSubBtn" type="primary" @click="submit">提 交</el-button> -->
                <el-button @click="dialogVisible = false">关 闭</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    //组件
    import TableCommon from "@/components/TableCommon.vue";
    import InfBox from "@/components/InfoBox.vue";
    import CusTable from "./components/CusTable.vue";
    import SearchInputs from "./components/SearchInputs.vue";
    import CusForm from "./components/CusForm.vue";
    import FilesList from "@/components/FilesList.vue";
    //接口
    import {
        getPointsList,
        exportRecord
    } from "@/api/archives.js";
    //公用函数
    import tool from "@/utils/tool.js";
    export default {
        extends: TableCommon,
        name: 'overview',
        components: {
            InfBox,
            CusTable,
            SearchInputs,
            CusForm,
            FilesList,
        },
        data() {
            return {
                //表单
                dialogVisible: false, //弹框显示控制
                showSubBtn: true, //弹窗中是否显示提交按钮
                formData: {}, //每个计量点的详细数据
                infoIems: [{
                    label: '计量点数量',
                    num: 0,
                }, {
                    label: '采集终端数量',
                    num: 0,
                }, {
                    label: '计量点在线数量',
                    num: 0,
                }, {
                    label: '计量点离线数量',
                    num: 0,
                }, {
                    label: '当季变更次数',
                    num: 0,
                }],
                clientId: '', //新增后的采集终端id
            }
        },
        computed: {

        },
        methods: {
            _getData(params = {}) { // 通过接口 获取最新数据 如果无接口 则是第一次加载
                params = {
                    ...this.page,
                    ...this.search,
                }
                params.datatype = "all";
                getPointsList(params).then(res => {
                    const data = res.list ? res.list.rows : [];
                    const gk = res.gk;
                    this.infoIems[0].num = gk.total;
                    this.infoIems[1].num = gk.live;
                    this.infoIems[2].num = gk.live;
                    this.infoIems[3].num = gk.dead;
                    this.infoIems[4].num = gk.change;
                    this.tableData = data;
                    this.total = res.list.total;
                })
            },
            //导出按钮
            downHand() {
                exportRecord({
                    datatype: 'all',
                    ...this.search,
                }).then(tool.down)
            },
            btnClick(name, row) {
                this.formData = row;
                this.clientId = row.gwId;
                if (name == '计量点信息') {
                    this.dialogVisible = true;
                }
            },
            //提交表单
            submit() {
                this.$refs['CusForm'].submitForm((data) => {})
                //this.dialogVisible = false;
            },
            //弹窗关闭
            handleClose() {
                this.dialogVisible = false;
            },
        }
    }
</script>

<style scoped lang="scss">
    .overview {
        margin-top: 0.24rem;

        .search {
            padding: 16px 0 0px 0;
        }
    }
</style>