<!--UploadProgress组件doc-->
<!--
    上传文件模拟进度条
参数说明：
-->
<template>
    <div>
        <div v-if="percent">
            <div>{{percent}}%</div>
            <div class="progress-line" :style="'width:'+percent+'%'"></div>
        </div>
    </div>
</template>
<script>
    export default {
        props: {
            // color: {
            //     type: String,
            //     default: '#2E4AF7'
            // },
        },
        data() {
            return {
                percent: 0,
                autoAddTimer: 0,
            }
        },
        methods: {
            init() {
                clearInterval(this.autoAddTimer);
                this.percent = 0;
            },
            start() {
                this.antoAddPrecent();
            },
            isLoading() {
                return this.percent !== 0 && this.percent !== 100;
            },
            success() {
                this.percent = 100;
                this.autoAddTimer && clearInterval(this.autoAddTimer)
                // this.$message.success('上传成功');
                setTimeout(() => {
                    this.percent = 0;
                }, 1000);

            },
            //自动增加进度条
            antoAddPrecent() {
                this.percent = 1;
                this.autoAddTimer = setInterval(() => {
                    this.percent += 1;
                    if (this.percent >= 99) {
                        this.percent = 99;
                        clearInterval(this.autoAddTimer);
                        this.autoAddTimer = 0;
                    }
                }, 100)
            },
        }
    };
</script>
<style scoped lang="scss">
    .progress-line {
        border-bottom: 1px solid #2E4AF7;
        transition: all 0.7s;
        margin-top: 10px;
    }
</style>