<template>
    <!--发起审批-->
    <el-dialog title="发起审批" custom-class="cusDialog" :visible.sync="show" width="4.75rem" :before-close="handleClose">
        <div class="content-title">人员列表</div>
        <div class="user-list">
            <el-checkbox-group v-model="checkList" @change="change">
                <el-checkbox v-for="(item,index) in data" :key="item.userId" :label="item.userId">
                    {{item.nickName}}</el-checkbox>
            </el-checkbox-group>
        </div>
        <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="submit">提 交</el-button>
            <el-button @click="handleClose">关 闭</el-button>
        </span>
    </el-dialog>
</template>
<script>
    import {
        Message
    } from 'element-ui';
    //接口
    import {
        getCheckperson
    } from "@/api/archives.js";
    export default {
        props: {
            show: {
                type: Boolean,
                default: false,
            }
        },
        data() {
            return {
                data: [],
                checkList: []
            }
        },
        mounted() {
            this.getData();
        },
        computed: {

        },
        methods: {
            change(data) { //只选择一个人
                if (data.length) this.checkList = [this.checkList.pop()];
            },
            getData() {
                getCheckperson().then(res => {
                    this.data = res.data.map(item => ({
                        userId: item.id,
                        nickName: item.realName,
                    }))
                })
            },
            handleClose() {
                this.$emit('selectPeople')
            },
            submit() {
                if (this.checkList.length === 0) {
                    Message.error({
                        message: '至少选择一个审批人',
                    })
                    return;
                }
                this.$emit('selectPeople', this.checkList)
            }
        },

    }
</script>

<style scoped lang="scss">
    .user-list {
        line-height: 30px;
        max-height: 200px;
        overflow-y: auto;
    }
</style>