<!--计量点主页-->
<template>
    <el-card class="box-card">
        <el-tabs v-model="activeName" @tab-click="tabClick">
            <el-tab-pane label="总览" name="0">
                <Overview ref="0"></Overview>
            </el-tab-pane>
            <el-tab-pane label="新增计量点" name="1">
                <AddPoint ref="1"></AddPoint>
            </el-tab-pane>
            <el-tab-pane label="计量点变更" name="2">
                <EditPoint ref="2"></EditPoint>
            </el-tab-pane>
        </el-tabs>
    </el-card>
</template>

<script>
    import Overview from "./Overview.vue";
    import AddPoint from "./AddPoint.vue";
    import EditPoint from "./EditPoint.vue";

    export default {
        name: 'index',
        components: {
            Overview,
            AddPoint,
            EditPoint,
        },
        data() {
            return {
                activeName: '0',
            }
        },
        methods: {
            tabClick({
                index
            }) {
                this.$refs[index] && this.$refs[index].reloadTable();
            }
        }
    }
</script>

<style scoped lang="scss">
    .box-card {
        width: 100%;

        /deep/ .el-card__body {
            padding: 0 0.24rem 0.24rem 0.24rem;
        }
    }
</style>