<template>
    <el-form :model="selectedObj" :rules="isEdit?rules:{}" ref="form"
        :class="isEdit?'form jpPoint':'form onlyShow jpPoint'" :disabled="!isEdit">
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="gsName">
                <div class="label"><span class="important">*</span>上级变电站：</div>
                <el-select :disabled="otherDisable" v-model="selectedObj.gsName" filterable placeholder="请选择变电站"
                    @change="change_GS">
                    <el-option v-for="item in gs" :key="item.code" :label="item.name" :value="item.code">
                    </el-option>
                </el-select>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="glCode">
                <div class="label"><span class="important">*</span>所属路线：</div>
                <el-select :disabled="otherDisable" filterable @change="item => handleSelect(item,'glCode')"
                    v-model="selectedObj.glCode" placeholder="请选择路线">
                    <el-option v-for="item in gl" :key="item.code" :label="item.name" :value="item.code">
                    </el-option>
                </el-select>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="kcCode">
                <div class="label"><span class="important">*</span>所属公司：</div>
                <el-select :disabled="otherDisable" v-model="selectedObj.kcCode" filterable placeholder="请选择所属公司"
                    @change="change_KC">
                    <el-option v-for="item in kc" :key="item.code" :label="item.fullName" :value="item.code">
                    </el-option>
                </el-select>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item>
                <div class="label">所属平台：</div>
                <el-autocomplete ref="autocomplete" @focus="autocompleteFocusFlag=true"
                    @blur="autocompleteFocusFlag=false" :disabled="otherDisable" v-model="selectedObj.kpName"
                    :fetch-suggestions="querySearchKp" placeholder="请选择平台" @select="item => handleSelect(item,'kp')"
                    clearable @clear="clear"></el-autocomplete>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="from">
                <div class="label"><span class="important">*</span>资产属性：</div>
                <el-select :disabled="otherDisable" v-model="selectedObj.jfrom" placeholder="请选择资产属性">
                    <el-option label="自有计量点" value="自有"></el-option>
                    <el-option label="国网计量点" value="国网"></el-option>
                </el-select>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="name">
                <div class="label"><span class="important">*</span>计量点名称：</div>
                <el-input :disabled="otherDisable" v-model="selectedObj.name" placeholder="如：35kV永昆二线阳101H1计量钻井">
                </el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="pointType">
                <div class="label"><span class="important">*</span>计量方式：</div>
                <el-select :disabled="otherDisable" v-model="selectedObj.jlTy" placeholder="请选择计量方式">
                    <el-option label="高供高计" value="1"></el-option>
                    <el-option label="高供低计" value="2"></el-option>
                </el-select>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="dbBrand">
                <div class="label"><span class="important">*</span>电能表厂家：</div>
                <el-input :disabled="otherDisable" v-model="selectedObj.dbBrand" placeholder="请输入厂家名称"></el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="dbModel">
                <div class="label"><span class="important">*</span>电能表型号：</div>
                <el-input :disabled="otherDisable" v-model="selectedObj.dbModel" placeholder="请输入电能表型号"></el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':importantDisable}">
            <el-form-item prop="dbCode">
                <div class="label"><span class="important">*</span>电能表编码：</div>
                <el-input :disabled="importantDisable" v-model="selectedObj.dbCode" placeholder="请输入电能表编码"></el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':importantDisable}">
            <el-form-item prop="gwCode">
                <div class="label"><span class="important">*</span>终端编码：</div>
                <el-input :disabled="importantDisable" v-model="selectedObj.gwCode" placeholder="请输入终端编码">
                </el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':importantDisable}">
            <el-form-item>
                <div class="label">网关设备编码：</div>
                <el-input :disabled="importantDisable" v-model="selectedObj.ocode" placeholder="请输入网关设备编码">
                </el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':importantDisable}">
            <el-form-item prop="gwNetcode">
                <div class="label"><span class="important">*</span>SIM卡号：</div>
                <el-input :disabled="importantDisable" v-model="selectedObj.gwNetcode" placeholder="请输入SIM卡号">
                </el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':importantDisable}">
            <el-form-item prop="byRl">
                <div class="label"><span class="important">*</span>变压器容量：</div>
                <el-input :disabled="importantDisable" v-model="selectedObj.byRl" placeholder="请输入变压器容量"></el-input>
            </el-form-item>
        </div>

        <div class="item" :class="{'disable':importantDisable}">
            <el-form-item prop="hgCt">
                <div class="label"><span class="important">*</span>CT变比：</div>
                <el-input :disabled="importantDisable" v-model="selectedObj.hgCt" placeholder="请输入CT变比"></el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':importantDisable}">
            <el-form-item prop="hgPt">
                <div class="label"><span class="important">*</span>PT变比：</div>
                <el-input :disabled="importantDisable" v-model="selectedObj.hgPt" placeholder="请输入PT变比"></el-input>
            </el-form-item>
        </div>

        <div class="item" :class="{'disable':importantDisable}">
            <el-form-item prop="dbRate">
                <div class="label"><span class="important">*</span>倍率：</div>
                <el-input :disabled="importantDisable" type="number" v-model="selectedObj.dbRate" placeholder="请输入倍率">
                </el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="gcAccount">
                <div class="label"><span class="important">*</span>户号：</div>
                <el-autocomplete ref="autocomplete1" @focus="autocompleteFocusFlag1=true"
                    @blur="autocompleteFocusFlag1=false" :disabled="otherDisable" v-model="selectedObj.gcAccount"
                    :fetch-suggestions="querySearchGc" placeholder="请选择户号" @select="item => handleSelectGcAccount(item)"
                    clearable @clear="clear1"></el-autocomplete>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="xh">
                <div class="label"><span class="important">*</span>计量点状态：</div>
                <el-select :disabled="otherDisable" v-model="selectedObj.status" placeholder="请选择计量点状态">
                    <el-option label="在运" value="在运"></el-option>
                    <el-option label="停运" value="停运"></el-option>
                </el-select>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="dbDima">
                <div class="label"><span class="important">*</span>电表底表码：</div>
                <el-input :disabled="otherDisable" v-model="selectedObj.dbDima" placeholder="请输入电表底表码"></el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="dbDimaTime">
                <div class="label"><span class="important">*</span>底表码抄录时间：</div>
                <el-date-picker :disabled="otherDisable" v-model="selectedObj.dbDimaTime" type="datetime"
                    placeholder="选择日期时间" default-time="12:00:00">
                </el-date-picker>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="dbLiveTime">
                <div class="label"><span class="important">*</span>电表有效期：</div>
                <el-date-picker :disabled="otherDisable" v-model="selectedObj.dbLiveTime" type="datetime"
                    placeholder="选择日期时间" default-time="12:00:00">
                </el-date-picker>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="installTime">
                <div class="label"><span class="important">*</span>安装时间：</div>
                <el-date-picker :disabled="otherDisable" v-model="selectedObj.installTime" type="datetime"
                    placeholder="选择日期时间" default-time="12:00:00">
                </el-date-picker>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="gps">
                <div class="label">安装坐标：</div>
                <el-input :disabled="otherDisable" v-model="selectedObj.gps" placeholder="经度,维度">
                </el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item>
                <div class="label">安装位置描述：</div>
                <el-input :disabled="otherDisable" v-model="selectedObj.place" placeholder="如箱变、杆塔">
                </el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item prop="installPerson">
                <div class="label"><span class="important">*</span>安装人员：</div>
                <el-input :disabled="otherDisable" v-model="selectedObj.installPerson" placeholder="请输入安装人员姓名">
                </el-input>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item>
                <div class="label"><span class="important">*</span>虚拟计量点：</div>
                <el-radio-group :disabled="otherDisable" v-model="selectedObj.isVr">
                    <el-radio label="1">是</el-radio>
                    <el-radio label="0">否</el-radio>
                </el-radio-group>
            </el-form-item>
        </div>
        <div class="item" :class="{'disable':otherDisable}">
            <el-form-item>
                <div class="label"><span class="important">*</span>备注：</div>
                <el-input :disabled="otherDisable" class="textarea" type="textarea" v-model="selectedObj.remark"
                    placeholder="备注" :autosize="{ minRows: 2, maxRows:10 }"></el-input>
            </el-form-item>
        </div>
    </el-form>
</template>
<script>
    //接口
    import {
        getGlList,
        getKpList,
        getKcList,
        getGsList,
        getOrgList,
        getGcAccQueryByPage,
        getPointDetail,
        getGcAccQuery
    } from "@/api/archives.js";
    import {
        rules
    } from "./js/temp.js"
    import {
        parseTime,
        debounce
    } from "@/utils/index.js";
    import tool from "@/utils/tool.js";
    let nextYearDate = new Date();
    nextYearDate.setFullYear(nextYearDate.getFullYear() + 1);
    let init = {
        "name": "", //名称
        "byRl": 0, //变压器容量
        "checkStatus": "", //审核状态(已审:1,未审:0)
        "code": "", //
        "dbBrand": "", //电表品牌-厂家
        "dbChecktime": new Date(), //校验日期
        "dbCode": "", //电表编码
        "dbDima": '0', //底码值
        "dbDimaTime": new Date(), //底码时间
        "dbFrom": "自有", //电表来源（自有，国网，能投）
        "dbLiveTime": new Date(), //电表有效期
        "dbModel": "", //电表型号
        "dbRate": 0, //电表倍率
        "jfrom": "自有", //资产属性（自有，国有）
        "gcAccount": "", //户号(供电公司比如国网缴费户号)
        "glCode": "", //线路编码
        "glName": "", //线路名称
        "gwCode": "", //网关编码
        "gwName": "", //采集器名称
        "gwNetcode": "", //物联网卡号
        "hgCt": "", //ct变比
        "hgPt": "", //pt变比
        "installPerson": "", ////安装人员
        "installTime": new Date(), //安装时间
        "jlTy": "1", //计量方式（1：高供高计，2：高供低计）
        "kcCode": "", //公司编码
        "kpCode": "", //平台编码
        "kpName": "", //平台名称

        "ocode": "", //	数据标识
        "otable": "", //超级表
        "pathDb": "", //	电能表检定合格证
        "pathDl": "", //电流互感器鉴定合格证
        "pathDs": "", //	现场验收评价表
        "pathDy": "", //	电压互感器鉴定合格证
        "pathXc1": "", //现场照片上传
        "status": "在运", //	计量点状态（在运，停运）
        "gsName": '', //变电站
        "ocode": '', //设备编码-通过此编码和数据平台进行关联
        "isVr": '0', //虚拟计量点为1，其他为0
        "lx": '', //x坐标
        "ly": '', //y坐标
        "gps": '', //xy坐标
        "place": '', //位置（杆塔，箱变）
        "remark": '', //备注
    }
    export default {
        props: {
            formData: Object,
            isEdit: { //是否是编辑状态
                type: Boolean,
                default: false,
            },
            editContentType: String, //变更类型 1：基础信息修改 2: 设备信息变更
            code: String, //新增记录的code
            jpChecked: {
                type: Number,
                default: 1,
            } //计量点是否通过状态
        },
        data() {
            return {
                rules: {
                    name: [{ //计量点名称规则  1.如果选择平台则线路+平台+其他，2.没有选择平台 则线路+其他
                        validator: (rule, value, callback) => {
                            const {
                                glName,
                                kpName
                            } = this.selectedObj;
                            let outLineStr;
                            if (!glName) {
                                outLineStr = value.slice(value.lastIndexOf('线') + 1);
                            } else {
                                outLineStr = value.split(glName)[1];
                            }
                            if (value.startsWith(' ')) {
                                callback(new Error('计量点名称不能以空格开头'));
                                return;
                            }
                            //判断是否为空
                            if (value === '') {
                                callback(new Error('计量点名称不能为空'));
                                return;
                            }
                            //判断是否包含线路名称且以线路开头
                            if (!glName || (glName && !value.startsWith(glName))) {
                                if (!glName) {
                                    callback('请先选择所属路线');
                                } else {
                                    callback(new Error(`计量点名称需以线路[${this.selectedObj.glName}]名称开头,请核查`));
                                }
                                return;
                            }
                            //判断是否包含平台名称且跟随线路
                            if (kpName && kpName !== '无' && !outLineStr.startsWith(kpName)) {
                                callback(new Error(`计量点名称需包含[${this.selectedObj.kpName}]且紧跟线路名称,请核查`));
                                return;
                            }
                            callback();
                        },
                        trigger: 'blur'
                    }],
                    gps: [{
                        validator: (rule, value, callback) => {
                            //判断是否为空
                            if (!value) {
                                callback();
                                return;
                            }
                            if (!value.includes(',')) {
                                callback(new Error('请用英文逗号隔开经度纬度'));
                                return;
                            }
                            const ps = value.split(',');
                            if (!tool.isGpsLon(ps[0])) {
                                callback(new Error('经度不正确'));
                                return;
                            }
                            if (!tool.isGpsLat(ps[1])) {
                                callback(new Error('维度不正确'));
                                return;
                            }
                            callback();
                        },
                        trigger: 'blur'
                    }],
                    ...rules
                }, // 校验规则
                gl: [], // 线路
                kp: [], //平台
                kc: [], //公司
                gs: [], //变电站
                selectedObj: {
                    ...init,
                },
                autocompleteFocusFlag: false,
                autocompleteFocusFlag1: false,
            }
        },
        mounted() {
            this.getData(); //获取下拉框数据
            this.querySearchGc = debounce(this.querySearchGc.bind(this), 300);
            this.querySearchKp = debounce(this.querySearchKp.bind(this), 300);
        },
        computed: {
            //重要字段变更
            importantDisable() {
                return this.isEdit && this.editContentType == 1
            },
            //基本字段变更
            otherDisable() {
                return this.isEdit && this.editContentType == 2
            }
        },
        methods: {
            //表单提交验证
            submitForm(success) {
                if (!this.isEdit) return;
                this.$refs['form'].validate((valid) => {
                    if (valid) {
                        if (this.code) this.selectedObj.code = this.code;
                        const copy = JSON.parse(JSON.stringify(this.selectedObj))
                        //去除字符串前后空格
                        try {
                            copy.name = copy.name.trim();
                            copy.dbBrand = copy.dbBrand.trim();
                            copy.installPerson = copy.installPerson.trim();
                        } catch (e) {
                            console.log(e);
                        }
                        //处理时间格式
                        copy.dbChecktime = parseTime(new Date(copy.dbChecktime));
                        copy.dbDimaTime = parseTime(new Date(copy.dbDimaTime));
                        copy.dbLiveTime = parseTime(new Date(copy.dbLiveTime));
                        copy.installTime = parseTime(new Date(copy.installTime));
                        if (copy.gps) {
                            copy.lx = copy.gps.split(',')[0];
                            copy.ly = copy.gps.split(',')[1];
                        }
                        success(copy);
                    } else {
                        return false;
                    }
                });
            },
            //搜索下拉平台
            querySearchKp(keyWord, cb) {
                const opt = {
                    pageNum: 1,
                    pageSize: 20,
                    kcCode: this.selectedObj.kcCode,
                    _loading: false, //不显示加载中图标
                }
                if (keyWord == '') {
                    this.selectedObj.kpCode = '';
                    this.selectedObj.kpName = '';
                }
                if (keyWord !== '') {
                    opt.keyWord = keyWord;
                }
                getKpList(opt).then(res => {
                    if (res.data) {
                        const list = res.data.map(item => ({
                            ...item,
                            value: item.name,
                        }))
                        cb(list);
                        if (list.length == 0) {
                            this.selectedObj.kpCode = '';
                            this.selectedObj.kpName = '';
                        }
                    } else {
                        cb([]);
                    }
                })
            },
            //搜索下拉户号
            querySearchGc(keyWord, cb) {
                const opt = {
                    pageNum: 1,
                    pageSize: 10,
                    _loading: false, //不显示加载中图标
                }
                if (keyWord == '') {
                    this.selectedObj.gcAccount = '';
                }
                if (keyWord !== '') {
                    opt.gcAcc = keyWord;
                }
                // if (keyWord.length < 2) return cb([]);
                getGcAccQuery(opt).then(res => {
                    if (res.data) {
                        const list = res.data.map(item => ({
                            ...item,
                            value: `${item.gcAccount}【${item.payer}】`,
                        }))
                        cb(list);
                        if (list.length == 0) {
                            this.selectedObj.gcAccount = '';
                        }
                    } else {
                        cb([]);
                    }
                })
            },
            getData() {
                this._updateGl();
                this._updateKc();
                this._updateGs();
            },
            handleSelectGcAccount(item) {
                this.selectedObj.gcAccount = item.gcAccount;
                // this.$refs['form'].validateField('gcAccount');
            },
            //选择公司 筛选平台
            change_KC(data) {
                const opt = {
                    kcCode: data
                }
                this.selectedObj.kpCode = ''; //重置平台选项
                this.selectedObj.kpName = ''; //重置平台选项
                this.$refs['form'].validateField('kpCode');
            },
            //选择变电站 筛选线路
            change_GS(data) {
                const opt = {
                    gsCode: data
                }
                this._updateGl(data === 'all' ? {} : opt);
                this.selectedObj.glCode = ''; //重置线路选项
                this.$refs['form'].validateField('gsName');
            },
            //线路
            _updateGl(params) {
                getGlList(params).then(data => {
                    this.gl = data.data;
                })
            },
            //平台
            _updateKp(params) {
                getKpList(params).then(data => {
                    this.kp = data.data;
                })
            },
            //公司
            _updateKc(params) {
                getKcList(params).then(data => {
                    this.kc = data.data;
                })
            },
            //变电站
            _updateGs(params) {
                getGsList(params).then(data => {
                    this.gs = data.data;
                })
            },
            handleSelect(item, prop) {
                if (prop == 'kp') {
                    this.selectedObj.kpCode = item.code;
                    this.$refs['form'].validateField('kpCode');
                } else if (prop == 'glCode') {
                    this.selectedObj.glName = this.gl.find(n => n.code == item).name;
                    this.$refs['form'].validateField('glCode');
                    //选择线路自动关联户号
                    getGcAccQueryByPage({
                        glCode: item,
                    }).then(res => {
                        if (res.rows && res.rows.length) {
                            this.selectedObj.gcAccount = res.rows[0].gcAccount;
                        } else {
                            this.selectedObj.gcAccount = '';
                        }
                    })
                } else {
                    this.$refs['form'].validateField(prop);
                }
                if (prop == 'kp' || prop == 'glCode') {
                    if (!this.selectedObj.name) {
                        this.selectedObj.name = this.selectedObj.glName + (this.selectedObj.kpName == '无' ? '' : this
                            .selectedObj.kpName);
                    } else if (this.selectedObj.name == this.selectedObj.glName || this.selectedObj.name == this
                        .selectedObj.kpName) {
                        this.selectedObj.name = this.selectedObj.glName + (this.selectedObj.kpName == '无' ? '' : this
                            .selectedObj.kpName);
                    }
                    this.$refs['form'].validateField('name');
                }
            },
            getById(code) {
                getPointDetail({
                    jpcode: code,
                    checked: this.jpChecked,
                }).then(res => {
                    const obj = res.data;
                    this.reset();
                    if (obj) {
                        this._dealWithData(obj); //处理数据显示到页面
                    }
                })
            },
            //处理数据显示到页面
            _dealWithData(obj) {
                for (let key in obj) {
                    this.selectedObj[key] = obj[key];
                }
                this.selectedObj.isVr = this.selectedObj.isVr + '';
                if (this.selectedObj.lx) {
                    this.selectedObj.gps = this.selectedObj.lx + ',' + this.selectedObj.ly;
                } else {
                    this.selectedObj.gps = !this.isEdit ? '无' : '';
                }
                if (!this.isEdit) {
                    this.selectedObj.place = !this.selectedObj.place ? '无' : this.selectedObj.place;
                    this.selectedObj.remark = !this.selectedObj.remark ? '无' : this.selectedObj.remark;
                }
            },
            reset() {
                for (let key in init) {
                    this.selectedObj[key] = init[key];
                }
                this.$refs['form'] && this.$refs['form'].clearValidate();
            },
            clear() {
                if (this.autocompleteFocusFlag) this.$refs.autocomplete.activated = true;
            },
            clear1() {
                if (this.autocompleteFocusFlag1) this.$refs.autocomplete1.activated = true;
            }
        },
        watch: {
            formData: {
                handler(val) {
                    if (!val) return;
                    this.getData(); //更新数据
                    if (Object.keys(val).length === 0) {
                        this.reset();
                        return;
                    }
                    this._dealWithData(val); //处理数据显示到页面
                    this.$nextTick(() => {
                        if (this.isEdit) {
                            this.$refs['form'] && this.$refs['form'].validate()
                        }
                    })
                },
                deep: true,
                immediate: true,
            },
            code: {
                handler(val) {
                    if (!val) return;
                    this.getData(); //更新数据
                    this.getById(val);
                },
                immediate: true,
            },
        }
    }
</script>

