<!--CusTable组件doc-->
<!--
实用方式如下：  参考src\views\archives\calcPoint\overview.vue  计量方式
<CusTable :tableRows="tableRows" :tableData="tableData" :tableBtns="btnsColumns"
    :btnsColumnWidth="btnsColumnWidth" :btnsColumnName="btnsColumnName"
    @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange"></CusTable>

参数解释:
tableRows: {Array} 表格的表头定义
tableData: {Array} 表格的data数组 也就是接口返回的数据 
tableBtns: {Array} 表格的操作按钮的定义数组 格式 [{label: '删除'（可以是函数返回值即为按钮名称）}] 
btnsColumnWidth: {Number} 表格的宽度 默认100 缺省
btnsColumnName: {String} 操作栏的名称 默认’操作‘
@handleSizeChange: {String} 分页大小改变的事件名称
@handleCurrentChange: {String} 分页页数的事件名称

-->
<template>
    <div style="width: 100%" class="cus-table">
        <el-table style="width: 100%" :data="tableData" :header-cell-style="{background:'#E6EAFE',color:'#606266'}"
            :row-class-name="tableRowClassName">
            <el-table-column type="index" label="序号" width="90" />
            <el-table-column label="所属公司" prop="kcName" show-overflow-tooltip></el-table-column>
            <el-table-column label="所属平台" prop="kpName"></el-table-column>

            <el-table-column label="计量点名称" prop="jpName" width="250" show-overflow-tooltip></el-table-column>
            <el-table-column label="变更类型" prop="content">
                <template slot-scope="scope">
                    <span>{{content[scope.row['content']] }}</span>
                </template>
            </el-table-column>
            <el-table-column label="变更原因" prop="cause" show-overflow-tooltip></el-table-column>
            <el-table-column label="状态" prop="checkStatus" width="120">
                <template slot-scope="scope">
                    <span
                        :class="scope.row['checkStatus']==='1'?'green':'red'">{{statusArr[scope.row['checkStatus']!==null?scope.row['checkStatus']:0]}}</span>
                </template>
            </el-table-column>
            <el-table-column label="操作" align="left" fixed="right" class-name="cusTable" width="90">
                <template slot-scope="scope">

                    <span v-if="scope.row.checkStatus==3||scope.row.checkStatus==4" class="cus-btn"
                        @click="handClick(getLabel(scope.row),scope.row)" type="text" size="small">
                        {{getLabel(scope.row)}}
                    </span>
                    <el-dropdown v-else @command="handleCommand($event,scope.row)">
                        <span class="el-dropdown-link cus-btn">
                            查看<i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="审核信息">变更申请</el-dropdown-item>
                            <el-dropdown-item v-if="scope.row.content==1||scope.row.content==2" command="计量点信息">计量点信息
                            </el-dropdown-item>
                            <el-dropdown-item v-if="checkBtn&&scope.row.checkStatus==0" command="审核">审核
                            </el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </template>
            </el-table-column>
        </el-table>
        <div class="page pagination-box">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[10, 20, 50, 100]" :page-size="pageSize" layout="prev, pager, next, jumper,total, sizes"
                :total="total">
            </el-pagination>
        </div>
    </div>

</template>

<script>
    export default {
        name: 'table1',
        props: {

            tableData: { //数据
                type: Array,
                default: () => []
            },
            total: {
                type: Number,
                default: 0
            },
            pageSize: { //分页大小
                type: Number,
                default: 10
            },
            checkBtn: { //是否显示审核功能
                type: Boolean,
                default: false
            },
            pageNum: {
                type: Number,
                default: 1
            },

        },

        data() {
            return {
                content: ['', '基本信息变更', '设备信息变更', '计量点新增'],
                statusArr: ['未审核', '已审核', '', '待提交信息', '待发起审批'],
                replayId: '',
            }
        },
        created() {

        },

        mounted() {

        },
        methods: {
            tableRowClassName({
                row,
                rowIndex
            }) {
                if (row.id == this.replayId) {
                    return 'active-row';
                }
                return '';
            },
            handleSizeChange(data) {
                this.$emit('handleSizeChange', data)
            },
            handleCurrentChange(data) {
                this.$emit('handleCurrentChange', data)
            },
            getLabel(row) { //获取操作按钮名称 可能是函数则执行结果返回
                if (row.checkStatus === '3') {
                    return '去填写';
                } else if (row.checkStatus === '4') {
                    return '去发起';
                }
            },
            handClick(textName, row) {
                this.$emit('btnClick', textName, row);
                this.replayId = row.id.toString(); //记录激活的行
            },
            handleCommand(item, row) {
                this.$emit('btnClick', item, row);
                this.replayId = row.id.toString(); //记录激活的行
            }
        }
    }
</script>

<style scoped lang="scss">
    .page {
        display: flex;
        justify-content: flex-end;
        margin-top: 15px;
    }

    .cus-btn {
        color: #2E4AF7;
        font-size: 14px;
        padding: 0 2px;
        cursor: pointer;
    }
</style>