<template>
    <div class="overview cus">
        <Steps :items="['发起变更', '变更操作', '变更审核']" @click="stepClick" :disabled="stepDisabled" :active="stepActive">
        </Steps>
        <SearchInputs @downHand="downHand" @searchHand="searchHand" :type="2" @add="addHand" :showAddBtn="true">
        </SearchInputs>
        <CusTableChange ref="table" :tableData="tableData" @btnClick="btnClick" @handleSizeChange="handleSizeChange"
            @handleCurrentChange="handleCurrentChange" :pageSize="page.pageSize" :pageNum="page.pageNum" :total="total"
            :checkBtn="roles_checkPerson"></CusTableChange>
        <!--弹框 变更申请-->
        <el-dialog title="变更申请" custom-class="cusDialog" :visible.sync="this.dialog==1" width="4.5rem"
            :before-close="handleClose">
            <EditTypeForm ref="form" :formDataChange="formData" :isEdit="formChangeEdit"></EditTypeForm>
            <span slot="footer" class="dialog-footer">
                <el-button v-show="formChangeEdit" type="primary" @click="submit" :loading="loading">提 交</el-button>
                <el-button @click="dialog = 0">关 闭</el-button>
            </span>
        </el-dialog>
        <!--弹框 变更操作-->
        <el-dialog title="变更操作" custom-class="cusDialog" :visible.sync="this.dialog==2" width="9rem"
            :before-close="handleClose">
            <div class="content-title">基本信息</div>
            <CusForm ref="CusForm" :editContentType="editContentType" :code="pointCode" :isEdit="true" :jpChecked="0">
            </CusForm>
            <div class="content-title">附件信息</div>
            <div class="upload-div">
                <Upload ref="upload" :jpChecked="0" :code="pointCode"></Upload>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="submit" :loading="loading">提 交</el-button>
                <el-button @click="handleClose">关 闭</el-button>
            </span>
        </el-dialog>
        <!--弹框 审核-->
        <!--发起审核-->
        <SelectUser :show="this.dialog==3" @selectPeople="selectPeople"></SelectUser>

        <!--查看审核记录-->
        <el-dialog title="变更申请" custom-class="cusDialog" :visible.sync="dialogVisible_change" width="4.5rem"
            :before-close="handleCloseDetailChange">
            <EditTypeForm :formDataChange="showFormData" :isEdit="false"></EditTypeForm>
            <span slot="footer" class="dialog-footer">
                <el-button @click="handleCloseDetailChange">关 闭</el-button>
            </span>
        </el-dialog>
        <!--查看信息框-->
        <el-dialog title="计量点信息" custom-class="cusDialog" :visible.sync="dialogVisible" width="9rem"
            :before-close="handleCloseDetail">
            <div class="content-title">基本信息</div>
            <CusForm :code="showPointCode" :isEdit="false" :jpChecked="jpChecked"></CusForm>
            <div class="content-title">附件信息</div>
            <FilesList :formData="showFormDetailData" :code="showPointCode" :jpChecked="jpChecked"></FilesList>
            <span slot="footer" class="dialog-footer">
                <el-button @click="handleCloseDetail">关 闭</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    import TableCommon from "@/components/TableCommon.vue";
    import InfBox from "@/components/InfoBox.vue";
    import FilesList from "@/components/FilesList.vue";
    import CusTableChange from "./components/CusTableChange.vue";
    import Steps from "@/components/Steps.vue";
    import SearchInputs from "./components/SearchInputs.vue";
    import EditTypeForm from "./components/EditTypeForm.vue";
    import CusForm from "./components/CusForm.vue";
    import SelectUser from "./components/SelectUser.vue";
    import Upload from "./components/Upload.vue";
    //接口
    import {
        postJpointChange,
        getPointChangeList,
        getPointChangeCheck,
        postUpdatePoint,
        doCheckChangePoint,
        exportJpointChange,
        postJpointChangeUpdate,
    } from "@/api/archives.js"
    //公用函数
    import tool from "@/utils/tool.js";
    import {
        mapState
    } from 'vuex';
    import {
        ROLENAME
    } from "@/utils/config.js";
    export default {
        extends: TableCommon,
        name: 'overview',
        components: {
            InfBox, //信息框
            CusTableChange, //表格
            Steps, //步骤条
            SearchInputs, //搜索表单
            EditTypeForm, //变更内容申请表单
            CusForm, //变更操作表单
            SelectUser, //变更审核
            Upload, //文件上传
            FilesList //文件查看
        },
        data() {
            return {
                dialogVisible: false, //计量点详情弹框
                dialogVisible_change: false, //审核信息详情弹框
                formDetailData: {}, // 计量点详情数据
                showFormDetailData: {}, // 查看计量点详情数据
                pointCode: '', //修改计量点点信息选择的计量点code
                showPointCode: '', //查看计量点弹框code
                formData: {}, // 发起申请数据
                showFormData: {}, // 查看审批申请弹框 详情
                formChangeEdit: true, //是否可编辑
                stepActive: 0, //当前步骤条状态
                dialog: 0, // 当前弹框类型 1 终端录入 2 计量点录入 3资料上传 4发起审核
                loading: false, // 提交按钮loading状态

                stepDisabled: [], //禁止步骤条数组
                editContentType: '1', //计量点变更类型 控制表单的输入范围
                jpChecked: '0', //查看计量点信息时传入的状态 是否审核成功   0：未审核 1：已审核
            }
        },
        computed: {
            ...mapState({ //是否是审核人员
                roles_checkPerson: state => state.permission.roles.includes(ROLENAME.checkPerson),
                userId: state => state.user.userId, //当前登录的user
            })
        },
        methods: {
            _getData(params = {}) { // 通过接口 获取最新数据 如果无接口 则是第一次加载
                params = {
                    ...this.page,
                    ...this.search
                }
                getPointChangeList(params).then(res => {
                    const data = res.list ? res.list.rows : [];
                    this.tableData = data;
                    this.total = res.list.total;
                })
            },
            //步骤条点击
            stepClick(item) {
                switch (item) {
                    case '发起变更':
                        this.dialog = 1;
                        break;
                    case '变更操作':
                        this.dialog = 2;
                        // this.editContentType = row.content; //变更类型
                        break;
                    case '变更审核':
                        this.dialog = 3;
                        break;
                }
            },
            //操作按钮点击的事件
            btnClick(name, row) {
                this.editContentType = row.content; //变更类型
                if (name == '审核') {
                    this.toExam(row);
                    return;
                }

                if (row.content == 3) { //计量点新增 没有变更操作步骤
                    this.stepDisabled = ['变更操作'];
                } else {
                    this.stepDisabled = [];
                }
                if (name == "审核信息") { //查看审查信息
                    this.showFormData = JSON.parse(JSON.stringify(row));
                    this.dialogVisible_change = true;
                    this.showPointCode = row.jpCode;

                } else if (name == "计量点信息") { //查看计量点信息
                    this.jpChecked = row.checkStatus * 1; //是否审核
                    this.dialogVisible = true;
                    this.showPointCode = row.jpCode;
                    this.showFormDetailData = JSON.parse(JSON.stringify(row));

                } else if (name == '去填写') { //修改计量点信息 
                    this.pointCode = row.jpCode; //变更的计量点id
                    this.formChangeEdit = false; // 改为不可更改
                    this.stepActive = 1;
                    this.dialog = 2;
                    this.changeId = row.id; //记录当前复盘的审批单id
                    this.formData = JSON.parse(JSON.stringify(row));
                } else if (name == '去发起') { //发起审批
                    this.pointCode = row.jpCode; //变更的计量点id
                    this.formChangeEdit = false; // 改为不可更改
                    this.stepActive = 2;
                    this.dialog = 3;
                    this.changeId = row.id; //记录当前复盘的审批单id
                    this.formData = JSON.parse(JSON.stringify(row));
                }
            },
            //审核计量点
            toExam(row) {
                this.$confirm('请选择审核结果', '信息审核', {
                    confirmButtonText: '通过',
                    cancelButtonText: '不通过',
                    distinguishCancelAndClose: true,
                    type: 'warning'
                }).then(() => {
                    this._check(row, 1)
                }).catch(action => {
                    if (action == 'close') return;
                    this._check(row, 0)
                });
            },
            //审批
            _check(row, isPass = 1) {
                doCheckChangePoint({
                    userId: this.userId,
                    changeId: row.id,
                    isPass,
                }).then(res => {
                    this.$message({
                        type: 'success',
                        message: '操作成功!'
                    });
                    this.reloadTable()
                })
            },
            //导出按钮
            downHand() {
                exportJpointChange({
                    ...this.search,
                }).then(tool.down)
            },

            // 新增
            addHand() {
                if (this.stepActive != 0) {
                    this.clearFrom(); //清空步骤条数据
                    this.stepActive = 0; // 步骤还原
                }
                this.dialog = 1; //打开新增框
            },
            //关闭弹框
            handleClose(data) {
                this.dialog = 0;
            },
            //关闭查看信息框
            handleCloseDetail(data) {
                this.dialogVisible = false;
                this.clearFrom(true);
            },
            //关闭查看审核信息框
            handleCloseDetailChange(data) {
                this.dialogVisible_change = false;
                this.clearFrom(true);
            },
            //弹框提交按钮
            submit(data) {
                if (this.dialog == 1) { //提交审核表单
                    this.$refs['form'].submitForm((data) => {
                        this.loading = true;
                        const copy = {
                            ...data,
                        }
                        if (data.content == 3) { // 计量点新增 状态改为待发送审批
                            copy.checkStatus = '4';
                        } else {
                            copy.checkStatus = '3';
                            if (data.jpCode) copy.ejpCode = data.jpCode;
                        }
                        postJpointChange(copy).then(res => {
                            this.pointCode = res.data.jpCode; //记录选择的计量点
                            this.changeId = res.data.id; //记录当前复盘的审批单id

                            this.formChangeEdit = false; //发起变更申请后不可修改
                            if (data.content == 3) {
                                this._nextStep(2); // 跳到下一步骤
                                this.stepDisabled = ['变更操作'];
                                //当前步骤增加 
                                this.stepActive = 2;
                            } else {
                                this._nextStep(); // 跳到下一步骤
                                this.stepActive = 1;
                                this.editContentType = data.content; //变更类型
                            }
                        }).catch(err => {
                            this.loading = false; //加载
                        })
                    })
                } else if (this.dialog == 2) { //提交变更内容表单
                    this.$refs['upload'].submitForm((filesData) => {
                        this.$refs['CusForm'].submitForm((data) => {
                            // return;
                            this._updatePoint({
                                ...data,
                                ...filesData
                            });
                            postJpointChangeUpdate({
                                id: this.changeId,
                                checkStatus: 4,
                            }).then(res => {})
                            if (this.stepActive === 1) this.stepActive = 2;
                        })
                    })
                }
            },
            // 变更审核
            selectPeople(user) {
                this.dialog = 0;
                if (!user) { //取消选择审批人
                    return;
                }
                //发起
                this.loading = false; //加载
                getPointChangeCheck({
                    changeId: this.changeId, // 审批单唯一id
                    userId: user.toString(),
                }).then(res => {
                    //当前步骤增加 
                    this.loading = false; //加载
                    this.stepActive = 0; //步骤跳转到开始
                    this.dialog = 0; //关闭弹窗
                    this.$message({
                        title: '成功',
                        message: '提交成功',
                        type: 'success',
                        duration: 3000,
                    })
                    this.reloadTable(); //刷新表格数据    
                    this.clearFrom();
                }).catch(err => {
                    this.loading = false; //加载
                })
            },
            //修改计量点公共方法
            _updatePoint(opt) {
                //更新保存的对象记录
                for (let key in opt) {
                    this.formDetailData[key] = opt[key];
                }
                //修改计量点信息
                return postUpdatePoint(this.formDetailData).then(res => {
                    this.loading = false; //加载
                    if (res.data) {
                        this.loading = false; //加载
                        this._nextStep(); // 跳到下一步骤
                    }
                }).catch(err => {
                    this.loading = false; //加载
                })
            },
            //延迟跳转到下一个步骤弹框
            _delayAddDialog(stepNum) {
                const old = this.dialog;
                this.dialog = 0;
                setTimeout(() => {
                    this.dialog = old + stepNum;
                }, 1500)
            },
            //跳转到下一步提示并打开下一个弹框
            _nextStep(stepNum = 1) {
                this.$notify({
                    title: '成功',
                    message: '提交成功，即将跳转到下一步骤',
                    type: 'success',
                    duration: 3000,
                });
                this.loading = false;
                this._delayAddDialog(stepNum); //下一步弹框显示
                this.reloadTable(); //刷新表格数据
            },
            // 清空页面弹框内容
            clearFrom(isShow) {
                if (isShow) {
                    this.showFormData = {};
                    this.showFormDetailData = {};
                    this.showPointCode = '';
                    return;
                }
                this.formData = {};
                this.formChangeEdit = true;
                this.pointCode = null;
                this.changeId = null;
                this.formDetailData = {};
                this.stepDisabled = [];
                this.stepActive = 0; //步骤跳转到第一步
            },
        }
    }
</script>

<style scoped lang="scss">
    .overview {
        margin-top: 0.24rem;

        .search {
            padding: 16px 0 0px 0;

            .li {
                &:last-child {
                    margin-bottom: 0;
                }

                display: flex;
                margin-bottom: 16px;

                .item {
                    width: 25%;
                    display: flex;
                    line-height: 32px;
                    height: 32;

                    .label {
                        min-width: 70px;
                        margin-right: 2px;
                    }

                    .rt {
                        flex: 1;
                    }

                    .btns {
                        width: 100%;
                        display: flex;
                        justify-content: flex-end;
                    }
                }
            }
        }

        /deep/ .item .el-input__inner {
            width: 270px;
            height: 32px;
            line-height: 32px;
        }

        .upload-div {
            padding: 0 40px;
        }
    }
</style>